import { ajax } from "@/common/js/ajax";

export function getThisUserInfo() {
  return ajax({
    api: '/getThisUserInfo',
    method: 'get'
  });
}

export function setUserInfo(userInfoData) {
  return ajax({
    api: '/setUserInfo',
    method: 'post',
    data: userInfoData
  })
}



